<!--管理中心-->
<template>
  <div id='manager'>


    <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="200px" class="demo-ruleForm">

      <h3 class="alter">修改你的信息</h3>
      <el-form-item label="旧密码:" prop="oldpass" style="margin-right: 150px;">
        <el-input type="password" v-model="oldpass" autocomplete="off" style="width: 250px;"></el-input>
      </el-form-item>
      <el-form-item label="新密码:" prop="pass" style="margin-right: 150px;">
        <el-input type="password" v-model="ruleForm2.pass" autocomplete="off" style="width: 250px;"></el-input>
      </el-form-item>
      <el-form-item label="确认新密码:" prop="checkPass" style="margin-right: 150px;">
        <el-input type="password" v-model="ruleForm2.checkPass" autocomplete="off" style="width: 250px;"></el-input>
      </el-form-item>
      <el-form-item label="个人邮箱:" prop="email" style="margin-right: 150px;">
        <el-input  v-model="email" autocomplete="off" style="width: 250px;"></el-input>
      </el-form-item>
      <el-form-item label="头像:">
            <el-upload
            class="avatar-uploader"
            :action="net"
                   :show-file-list="false"
                   :on-success="handleAvatarSuccess"
                   :before-upload="beforeAvatarUpload">
          <img v-if="avatar"
               :src="avatar"
               class="avatar"
               style="width: 80px;margin-left: -200px;">
          <i v-else
             class="el-icon-plus avatar-uploader-icon" style="margin-left: -300px;"></i>
          </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm2')" style="margin-left: -150px;">提交</el-button>
        <el-button @click="resetForm('ruleForm2')">重置</el-button>
      </el-form-item>
    </el-form>

  </div>
</template>

<script>
  export default {
    data() {
      var validateOldPass = (rule, value, callback) => {
        var oldpwd
        let studentId = this.$cookies.get("cid")
          this.$axios({
            url:'/api/student/'+studentId,
            method:'get'
          }).then(res=>{
            oldpwd=res.data.data.pwd

          })
        setTimeout(() => {
          
          if (this.oldpass!=oldpwd) {
            callback(new Error('请输入正确的密码'));
          } 
          else {
              callback();
          }
        }, 1000);
      };
      var checkEmail = (rule, value, callback) => {
        setTimeout(() => {
          if (this.email.indexOf("@")==-1&&this.email.indexOf(".com")==-1) {
            callback(new Error('请输入合法的邮箱的地址'));
          } 
          else {
              callback();
          }
        }, 1000);
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.ruleForm2.checkPass !== '') {
            this.$refs.ruleForm2.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm2.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        net:"/api/upload",
        avatar:null,
        ispass: true,
        email:'',
        oldpass:'',
        ruleForm2: {
          pass: '',
          checkPass: ''
        },
        rules2: {
          oldpass: [
            { validator: validateOldPass, trigger: 'blur' }
          ],
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
          email:[
            {validator:checkEmail,trigger:'blur'}
          ]
        }
      };
    },
    created(){
      this.init()
    },
    methods: {
      init(){
        let studentId = this.$cookies.get("cid")
        this.$axios({
          url:'/api/student/'+studentId,
          method:'get'
        }).then(res=>{
          this.email=res.data.data.email
          this.avatar=res.data.data.avatar
        })
      },
      handleAvatarSuccess (res, file) {
      // this.imageUrl = URL.createObjectURL(file.raw);
      this.avatar=res.data
    // 可能URL.createObjectURL(file.raw);会出现路径错误
    // 可以替换为：this.imageUrl = res.data;
    },
    beforeAvatarUpload (file) {
      // const isJPG = file.type === 'image/png';
      const isLt2M = file.size / 1024 / 1024 <2;
      // if(!isJPG){
      //   this.$message.error('上传头像图片只能是 png 格式!');
      // }
      if(!isLt2M){
        this.$message.error('上传头像图片大小不能超过 2MB!');
      }
      return isLt2M;
    },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let studentId = this.$cookies.get("cid")
            this.$axios({ //修改密码
              url: '/api/studentPWD',
              method: 'put',
              data: {
                avatar:this.avatar,
                pwd: this.ruleForm2.pass,
                studentId,
                email:this.email
              }
            }).then(res => {
              this.$cookies.set("image",this.avatar)
              if(res.data != null ) { //修改成功提示
                this.$message({
                  message: '密码修改成功...',
                  type: 'success'
                })
              }
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.avatar=null
        this.$refs[formName].resetFields();
        this.email=''
      }
    }
  }
</script>

<style scoped>
#manager .pass  label{
  color: red;
  font-size: 20px;
}
#manager {
  width: 600px;
  margin: 0 auto;
  margin-top: 100px;
  text-align: center;
  /* margin-bottom: 300px; */
}
#manager .alter {
  margin: 30px 0px;
}
.avatar-uploader .el-upload {
    border: 1px dashed blue;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 80px;
    height: 80px;
    line-height: 80px;
    text-align: center;
  }
  .avatar {
    width: 80px;
    height: 80px;
    display: block;
  }
</style>



